<template lang="pug">
.my-lounge

  Appbar

  v-tabs.elevation-0(dark v-model="currentTab" color="primary")
    // @TODO: Paths don't matter here, only route names. Make issue to Vuetify.
    v-tab(v-bind:to="{ name: 'lounge' }" ripple) Popular Guestss
    v-tab(v-bind:to="{ name: 'lounge' }" ripple) Popular Messages

  .my-lounge__hero
    img.my-lounge__cone(src='~/@/assets/images/traffic-cone.svg' alt='Under Construction')
    div
      |work-in-progress

</template>

<script>
export default {
  name: 'Lounge',

  data () {
    return {
      currentTab: '/lounge'
    }
  },

  mounted () {
    // this.$store.dispatch('common/updateToolbar', {
    //  elevation: false
    // })
  },

  methods: {
  }
}
</script>

<style lang="stylus" scoped>
.my-lounge
  height 100%

  &__hero
    height 100%
    display flex
    justify-content center
    align-items center
    flex-direction column
    opacity 0.4

  &__cone
    width 100px
</style>



